import React, { cloneElement, useCallback, useEffect, useRef, useState, Fragment } from 'react';
import { element, elementType, number, oneOf, oneOfType, shape, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import { Manager, Reference, Popper } from 'react-popper';
import Menu from '../menu';

const CSS_PREFIX = 'vds-navmenu';
const CSS_POPPER_CONTAINER = 'vds-popper-container';

const keyCode = {
  DOWN: 40,
  END: 35,
  ESC: 27,
  HOME: 36,
  PAGEDOWN: 34,
  PAGEUP: 33,
  RETURN: 13,
  SPACE: 32,
  TAB: 9,
  UP: 38
};

const Navmenu = ({
  children,
  className,
  id = shortid.generate(),
  menu,
  modifiers,
  placement,
  tag: Tag,
  ...remainingProps
}) => {
  const [open, setOpen] = useState(false);
  const [focusVisible, setFocusVisible] = useState(false);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const listboxRef = useRef();
  const buttonRef = useRef();

  const handleClick = useCallback(e => {
    const isButton = e.target.tagName.toLowerCase() === 'button';
    const isLink = e.target.tagName.toLowerCase() === 'a';

    setOpen(!open);
    if (!isButton && !isLink) {
      e.preventDefault();
    }
  }, [open, setOpen]);

  useEffect(() => {
    if (!open) {
      return;
    }
    const handleClickOutside = e => {
      const menuEl = document.querySelector(`#${CSS_PREFIX}-menu-${uid}`);
      if (buttonRef.current.contains(e.target) || menuEl.contains(e.target)) {
        // let the button handle its own clicking functionality
        // and don't close if the menu itself is clicked
        return;
      }

      // the click is outside, close the menu
      handleClick(e);
    };
    document.addEventListener('click', handleClickOutside);
    document.addEventListener('touchend', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
      document.removeEventListener('touchend', handleClickOutside);
    };
  }, [open, handleClick, buttonRef, uid]);

  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const listbox = listboxRef.current;
    const lastLink = listbox.lastChild.querySelector('a');

    if (code === keyCode.TAB) {
      if (e.target.isEqualNode(lastLink)) {
        handleClick(e);
      }
    }
  };

  const handleKeyUp = e => {
    const code = e.charCode || e.keyCode;

    if (code === keyCode.TAB) {
      setFocusVisible(true);
    }
  };

  const handleBlur = () => {
    setFocusVisible(false);
  };

  return (
    <Manager>
      <Tag className={classnames(CSS_PREFIX, className)} {...remainingProps}>
        <Popper modifiers={modifiers} placement={placement}>
          {({ ref, style, scheduleUpdate }) => (
            <Fragment>
              <Reference>
                {({ ref }) => (
                  <span ref={ref} className={CSS_POPPER_CONTAINER}>
                    {cloneElement(children, {
                      active: open,
                      'aria-controls': `${CSS_PREFIX}-listbox-${uid}`,
                      'aria-expanded': open ? 'true' : 'false',
                      className: focusVisible ? 'focus-visible' : '',
                      id: uid,
                      onBlur: handleBlur,
                      onClick: e => {
                        // the popper position is not always positioned properly until it re-renders after being shown
                        scheduleUpdate();
                        handleClick(e);
                      },
                      onKeyUp: handleKeyUp,
                      ref: buttonRef
                    })}
                  </span>
                )}
              </Reference>
              <Menu
                id={`${CSS_PREFIX}-menu-${uid}`}
                position="absolute"
                ref={ref}
                show={open}
                style={{ ...style, overflowY: 'inherit' }}
              >
                {cloneElement(menu, {
                  'aria-hidden': open ? 'false' : 'true',
                  'aria-labelledby': uid,
                  id: `${CSS_PREFIX}-listbox-${uid}`,
                  onClick: handleClick,
                  onKeyDown: handleKeyDown,
                  ref: listboxRef
                })}
              </Menu>
            </Fragment>
          )}
        </Popper>
      </Tag>
    </Manager>
  );
};

Navmenu.propTypes = {
  /**
   * @ignore
   */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Menu
   */
  menu: element.isRequired,

  /**
   * Modifiers
   */
  modifiers: shape({}),

  /**
   * Placement
   */
  placement: oneOf([
    'auto',
    'bottom-end',
    'bottom-start',
    'bottom',
    'left-end',
    'left-start',
    'left',
    'right-end',
    'right-start',
    'right',
    'top-end',
    'top-start',
    'top'
  ]),

  /**
  * Should be tag name and not an actual element
  */
  tag: elementType
};

Navmenu.defaultProps = {
  modifiers: {
    offset: {
      enabled: true,
      offset: '0'
    }
  },
  placement: 'bottom-start',
  tag: 'div'
};

Navmenu.displayName = 'Navmenu';

export default Navmenu;
